import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import VueScrollTo from 'vue-scrollto';

Vue.use(VueScrollTo, {
    container: 'body',
    duration: 1000,
    easing: 'ease',
});

Vue.config.productionTip = false

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
